<template lang="html">
  <div class="w3-row section-content-tab">
    <div class="section-nav-container">
      <transition name="slideRightLeft">
        <div v-show="showNavBar" class="vision-nav-cell light-grey drop-shadow-br w3-border-top">
          <app-model-nav ></app-model-nav>
        </div>
      </transition>
    </div>
    <div @click="showNavBar = !showNavBar" class="hide-nav-div drop-shadow-br" :class="{'hide-nav-div-shift': showNavBar}">
      <i v-if="showNavBar" class="fa fa-chevron-left" aria-hidden="true"></i>
      <i v-else class="fa fa-chevron-right" aria-hidden="true"></i>
    </div>
    <div class="vision-content">
      <router-view></router-view>
    </div>
  </div>
</template>

<script>
import ModelNav from '@/components/model/nav/ModelNav.vue'

export default {
  components: {
    'app-model-nav': ModelNav
  },

  data () {
    return {
      showNavBar: true
    }
  },

  computed: {
    initiative () {
      return this.$store.state.initiative.initiative
    }
  },

  methods: {
    redirect () {
      if (this.$route.name === 'InitiativeModel') {
        let currentSection = this.$store.getters.currentSection
        if (currentSection !== null) {
          this.$router.replace({name: 'ModelSectionContent', params: {sectionId: currentSection.id}})
        } else {
          this.$router.replace({name: 'ModelSectionContent', params: {sectionId: this.initiative.topModelSection.id}})
        }
      }
    }
  },

  created () {
    this.redirect()
  }
}
</script>

<style scoped>

.section-content-tab {
  height: 100%;
  display: flex;
  flex-direction: row;
  position: relative;
}

.section-nav-container{
  height: 100%;
}

.vision-nav-cell {
  width: 350px;
  min-height: 1px;
  border-color: #e3e6e8 !important;
}

.vision-content {
  flex-grow: 1;
  height: 100%;
  min-width: 350px;
  display: flex;
  flex-direction: column;
}

.hide-nav-div {
  width: 25px;
  height: 35px;
  position: absolute;
  top: 12px;
  background-color: #313942;
  border-top-right-radius: 5px;
  border-bottom-right-radius: 5px;
  color: white;
  padding-top: 7px;
  padding-left: 6px;
  cursor: pointer;
}

.hide-nav-div-shift {
  left: 350px;
}

.hide-nav-div:hover {
  background-color: #3e464e;
}

</style>



